//variables
$background-color: rgb(234, 234, 235);
$sky-color: rgb(153, 192, 200);
$cloud-color: rgb(251, 251, 253);

$border-color: rgb(22, 71, 92);

$marron-color: rgb(161, 109, 94);
$marron_lighten-color: rgb(202, 168, 133);

$bleu-color: rgb(149,192,211);
$bleu_lighten-color: rgb(179,207,221);
$bleu_darken-color: rgb(107,152,172);

$grey-color: rgb(199,198,198);
$grey_lighten-color: rgb(226,226,224);
$grey_darken-color: rgb(159,160,166);

$red-color: rgb(224,116,102);
$red_lighten-color: rgb(241,148,125);
$red_darken-color: rgb(231,207,96);

$yellow-color: rgb(225,222,170);
$yellow_lighten-color: rgb(255,237,212);
$yellow_darken-color: rgb(255,207,147);

$green-color: rgb(101,196,94);
$green_darken-color: rgb(71,175,65);


//mixins
@mixin bg-degrad_3($color_1, $color_2, $color_3) {
  background: $color_2;
  background: -moz-linear-gradient(left, $color_1 25%, $color_2 28%, $color_2 75%, $color_3 78%, $color_3 75%);
  background: -webkit-linear-gradient(left, $color_1 25%, $color_2 28%, $color_2 75%, $color_3 78%, $color_3 75%);
  background: linear-gradient(to right, $color_1 25%, $color_2 28%, $color_2 75%, $color_3 78%, $color_3 75%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$color_1', endColorstr='$color_3',GradientType=1 ); }

@mixin border() {
  border: 4px solid $border-color; }

@mixin full_border-radius($radius) {
  -webkit-border-radius: $radius;
  border-radius:         $radius; }

@mixin border-radius($radius_1,$radius_2,$radius_3,$radius_4) {
  -webkit-border-radius: $radius_1 $radius_2 $radius_3 $radius_4;
  border-radius:         $radius_1 $radius_2 $radius_3 $radius_4; }

@mixin transform($transforms) {
  -webkit-transform: $transforms;
  transform:         $transforms; }

@mixin rotate ($deg) {
    @include transform(rotate(#{$deg}deg)); }

@mixin scale($scale) {
    @include transform(scale($scale)); }

@mixin translate ($x, $y, $z) {
    @include transform(translate3d($x, $y, $z)); }

@mixin skew ($x, $y) {
    @include transform(skew(#{$x}deg, #{$y}deg)); }

@mixin transform-origin ($origin) {
    -webkit-transform-origin: $origin;
    -moz-transform-origin: $origin;
    -ms-transform-origin: $origin;
    transform-origin: $origin; }

@mixin box-shadow($params) {
  -webkit-box-shadow: $params;
  -moz-box-shadow: $params;
  box-shadow: $params; }

//content

#content {
  position : absolute;
  height : 400px;
  width : 550px;
  background-color : $background-color;

  div {
    position: absolute; }

  .floor {
    width : 70%;
    height : 4px;
    top: 80%;
    left: 15%;
    @include full_border-radius(2px);
    background-color: $border-color; }


  .lamp {
    top: calc(80% - 14px);
    left: 20%;
    width: 50px;
    height: 10px;
    @include border;
    @include border-radius(8px,8px,0px,0px);
    @include bg-degrad_3($grey_darken-color, $grey-color, $grey_lighten-color);

    &:after {
      @extend .lamp;
      position: absolute;
      content: "";
      top: -28px;
      left: 15px;
      width: 10px;
      height: 20px; } }

  .lampBase {
    @extend .floor;
    top: -145px;
    width: 4px;
    left: 22px;
    height: 120px;

    &:after {
      @extend .lampBase;
      position: absolute;
      content: "";
      @include rotate(65);
      top: -50px;
      height: 90px;
      left: 10px; }

    &:before {
      @extend .lampBase;
      position: absolute;
      content: "";
      @include transform-origin(50% 0%);
      top: -24px;
      height: 35px;
      left: 49px; } } }
